import axios from "axios";
import { AgGridVue } from "ag-grid-vue3";
import Swal from 'sweetalert2';
export default {
  name: "PlanOrderModal",
  components: {
    AgGridVue
  },
  props: {
    visible: {
      type: Boolean,
      required: true
    }
  },
  watch: {
    visible(newVal) {
      if (newVal) {
        this.planOrderList();
      }
    }
  },
  data() {
    return {
      rowData: [],
      searchType: "product_order_name",
      searchText: "",
      columnDefs: [{
        field: "product_order_code",
        headerName: "생산지시코드",
        flex: 1
      }, {
        field: "product_order_name",
        headerName: "생산지시명",
        flex: 2
      }, {
        field: "start_date",
        headerName: "시작일",
        flex: 1
      }, {
        field: "end_date",
        headerName: "종료일",
        flex: 1
      }, {
        field: "prod_name",
        headerName: "제품명",
        flex: 3
      }, {
        field: "finish_status",
        headerName: "상태",
        flex: 1,
        valueFormatter: params => {
          if (params.value === 'WS1') {
            return '지시 등록';
          } else if (params.value === 'WS2') {
            return '생산중';
          } else if (params.value === 'WS3') {
            return '생산 완료';
          } else if (params.value == 'WS4') {
            return '폐기 처분';
          }
        },
        cellStyle: params => {
          if (params.value === 'WS1') {
            return {
              textAlign: 'center',
              fontWeight: 'bold',
              color: '#6c757d'
            };
          } else if (params.value == 'WS2') {
            return {
              textAlign: 'center',
              fontWeight: 'bold',
              color: '#fd7e14'
            };
          } else if (params.value == 'WS3') {
            return {
              textAlign: 'center',
              fontWeight: 'bold',
              color: '#28a745'
            };
          } else if (params.value == 'WS4') {
            return {
              textAlign: 'center',
              fontWeight: 'bold',
              color: '#28a745'
            };
          }
        }
      }],
      gridOptions: {
        domLayout: "autoHeight",
        // singleClickEdit: true,               // 셀 편집 ON
        // suppressRowClickSelection: true,     // 행 클릭 선택 OFF
        suppressCellFocus: true,
        // 셀 포커스 OFF
        pagination: true,
        // 페이징 ON
        paginationPageSize: 5,
        // 한 페이지 보여질 행 수
        paginationPageSizeSelector: false,
        //paseSize 선택란 삭제
        defaultColDef: {
          // 열(컬럼) 기본 설정
          suppressMovable: true,
          // 컬럼을 드래그하여 이동하지 못하게
          resizable: false,
          // 컬럼 크기 조절 못하게
          sortable: false,
          // 정렬 못하게
          cellStyle: {
            textAlign: "center"
          } // 텍스트를 가운데 정렬
        }
      }
    };
  },
  mounted() {
    // 계획 지시 목록
    this.planOrderList();
  },
  methods: {
    // 모달창 닫기 이벤트
    close() {
      this.$emit("close");
    },
    // 계획지시 목록 조회 API
    planOrderList() {
      axios.get('/api/work/order/list').then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    // 계획 지시 목록 검색
    async searchProudctOrder() {
      await axios.get(`/api/work/order/list`, {
        params: {
          type: this.searchType,
          keyword: this.searchText
        }
      }).then(res => {
        this.rowData = res.data;
      }).catch(err => console.error(err));
    },
    // 그리드 행 클릭 메소드
    onRowClicked(params) {
      if (params.data.finish_status === 'WS3') {
        Swal.fire({
          title: '실패',
          text: '이미 생산이 완료된 지시입니다.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      } else if (params.data.finish_status === 'WS4') {
        Swal.fire({
          title: '실패',
          text: '폐기된 지시입니다.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      }
      this.$emit('selectPlanOrder', params.data);
      this.close();
    }
  }
};